/** ====================================================================================================================
// Variables - Global
// ================================================================================================================== */
.header {
  font-family: "Montserrat", sans-serif;
  width: 100%;
  margin: 0;
  padding: 0 0.5em;
  height: 5em;
  background: #34495e; }
  .header__inner {
    height: 100%;
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    align-items: center; }
  .header__logo {
    width: 1.875em;
    height: 1.875em;
    margin: 0 0.625em;
    z-index: 2;
    transition: filter 0.25s ease-in-out; }
  .header__nav {
    z-index: 1; }
  .header__nav-button {
    z-index: 2; }
    @media (min-width: 768px) {
      .header__nav-button {
        display: none; } }
  .header--full-width .header__inner {
    max-width: 100%; }
  .header--expanded .header__logo {
    filter: grayscale(100%); }
    .header--expanded .header__logo:hover {
      filter: grayscale(0); }
  @media (pointer: fine) {
    .header--expanded .header__nav-button:hover .nav-button__bar {
      background-color: #ff7979; } }
