/** ====================================================================================================================
// Variables - Global
// ================================================================================================================== */
.main {
  min-height: 2000px; }

.nav__item {
  border: none;
  background: transparent; }

.nav__link {
  font-family: "Raleway", sans-serif;
  color: #fff;
  border: none;
  background: transparent;
  display: block;
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  cursor: pointer; }
  .nav__link:focus {
    outline: none; }
  .nav__link--placeholder {
    cursor: pointer; }

@media (max-width: 767px) {
  .nav {
    position: fixed;
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0;
    transition: opacity 0.25s ease-in-out;
    pointer-events: none; }
    .nav:before {
      content: '';
      display: block;
      position: absolute;
      z-index: 1;
      height: 100%;
      width: 100%;
      background-color: #111;
      opacity: 0.8;
      transition: opacity 0.25s ease-in-out; }
    .nav--expanded {
      opacity: 1;
      pointer-events: initial; }
    .nav__list {
      position: relative;
      z-index: 1; }
    .nav__item {
      width: auto;
      margin: 0 0 0.5em 0; }
    .nav__link {
      font-size: 2.25em;
      transition: color 0.25s ease-in-out;
      width: auto;
      margin: 0 auto;
      padding: 0 0 0.33333em 0;
      position: relative; }
      .nav__link:after {
        content: '';
        display: block;
        position: absolute;
        bottom: 0.11111em;
        height: 4px;
        width: 100%;
        background-color: #ff7979;
        transform: scaleY(1) scaleX(0);
        transition: 0.35s ease-in-out; }
      .nav__link:hover {
        color: #ff7979; }
        .nav__link:hover:after {
          transform: scaleY(1) scaleX(1); } }
