  .nav-button--arrow.nav-button--expanded .nav-button__bar:nth-child(1), .nav-button--expanded.nav-button--arrow-up .nav-button__bar:nth-child(1), .nav-button--expanded.nav-button--arrow-left .nav-button__bar:nth-child(1) {
    transform: translateY(0.25em) translateX(0.25em) rotate(45deg) scaleX(0.5); }
