    transform: translateY(0.0625em) rotate(90deg) scaleX(0.6); }
  .nav-button--arrow.nav-button--expanded .nav-button__bar:nth-child(3), .nav-button--expanded.nav-button--arrow-up .nav-button__bar:nth-child(3), .nav-button--expanded.nav-button--arrow-left .nav-button__bar:nth-child(3) {
    transform: translateX(-0.25em) translateY(-0.75em) rotate(-45deg) scaleX(0.5); }
